<template>
<div class="">
  <div class="properties-comparison">
    <h3 class="title">
    Properties comparison
    </h3>
    <div class="search-condition">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0" class="comparison-ruleForm clearfixtr">
        <el-form-item label="" prop="material1" class="dinlineb" style="margin-left: 179px;">
          <el-select v-model="ruleForm.material1" placeholder="Please Choose" class="compare-select" style="width: 310px;">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
          <el-row>
              <el-popover
              ref="popover2"
              placement="bottom"
              width="200"
              trigger="click"
              content="Website is under construction, so stay tuned !">
              </el-popover>

              <el-col :span="24" class="text-center" v-popover:popover2>Specifications download</el-col>

          </el-row>
        </el-form-item>

        <span class="vs">
          VS
        </span>
        <el-form-item prop="material2" class="dinlineb">
          <el-select v-model="ruleForm.material2" placeholder="Please Choose" class="compare-select" style="width: 310px;">
            <el-option
              v-for="item in options2"
              :key="item.value"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
          <el-row>
            <el-popover
              ref="popover2"
              placement="bottom"
              width="200"
              trigger="click"
              content="Website is under construction, so stay tuned !">
            </el-popover>

            <el-col :span="24" class="text-center" v-popover:popover2>Specifications download</el-col>

          </el-row>
        </el-form-item>
        <el-form-item class="dinlineb">
          <input type="button" class="compare-btn"  @click="dialogVisible?dialogVisible:submitForm('ruleForm')" value="Contrast" />
          <el-dialog
            :show-close="false"
            :visible.sync="dialogVisible"
            width="20%">
            <span style="width:100%;text-align:center;display:block">请选择不同质量标准</span>
          </el-dialog>
        </el-form-item>
      </el-form>
    </div>
    <div class="">
      <h3 class="title">
        Quote Comparison
      </h3>
      <p class="mar-bottom-10"><label><input type="checkbox" @change="contrast()" v-model="checkedName"> Show Different Properties</label></p>
      <el-table
        :data="checkedName?tdata:tableData"
        stripe
        @header-click="aaa"
        style="width: 100%" :class="{diff:checkedName}" :row-class-name="tableRowClassName">
        <el-table-column
          prop="Properties"
          label="Properties"
          width="366">
        </el-table-column>
        <el-table-column
          prop="Units"
          label="Units"
          width="170">
        </el-table-column>
        <el-table-column :label="materialName1">
          <el-table-column
            prop="Min1"
            label="Min.">
          </el-table-column>
          <el-table-column
            prop="Max1"
            label="Max.">
          </el-table-column>
        </el-table-column>
        <el-table-column :label="materialName2">
          <el-table-column
            prop="Min2"
            label="Min.">
          </el-table-column>
          <el-table-column
            prop="Max2"
            label="Max.">
          </el-table-column>
        </el-table-column>
      </el-table>
    </div>
  </div>
</div>
</template>

<script>
  import {allData,createTableData} from '@/json'
  import dataURLtoBlob from '@/utils/toBlob.js'
  export default {
        name: "properties",
        data(){
          return {
            options: [{
              value: '1',
              name: 'GB 0# General diesel fuels 2015'
            }, {
              value: '2',
              name: 'GB(V) 0# AutomobiIe dieseI fuels 2016'
            }, {
              value: '3',
              name: 'GB(VI) 0# AutomobiIe dieseI fuels 2016'
            }, {
              value: '4',
              name: 'Astralian diesel fuels'
            }, {
              value: '5',
              name: 'Australian Automotive Diesel  2001'
            }, {
              value: '6',
              name: 'Sinochem QZ ulsd (Astralia)'
            }, {
              value: '7',
              name: 'Philippine diesel fuels'
            }, {
              value: '8',
              name: 'Sinochem QZ 50ppm diesel fuels (Philippine)'
            }, {
              value: '9',
              name: 'USA MVNRLM Motor vehicle diesel fuel'
            }, {
              value: '10',
              name: 'Indonesia HSD'
            }, {
              value: '11',
              name: 'Hong Kong Motor Vehicle  ULSD'
            }, {
              value: '12',
              name: 'India HSD'
            }],
            options2: '',
            value: '',
            ruleForm:{
              material1:'',
              material2:''
            },
            tableData:[],
            tdata:[],
            dialogVisible:false,
            materialName1:' - ',
            materialName2:' - ',
            checkedName:false,
            rules:{
              material1: [
                { required: true, message: ' ', trigger: 'change' }
              ],
              material2: [
                { required: true, message: ' ', trigger: 'change' }
              ],
            },
            copyCreateTableData: JSON.parse(JSON.stringify(createTableData)),
          }
        },
        watch:{
            '$route.path'(to,from){
                this.matchComparison()
            }
        },
        components:{},
        mounted(){
          this.options2 = JSON.parse(JSON.stringify(this.options))
          this.matchComparison()
        },
        methods:{
          tableRowClassName({row, rowIndex}) {
            if (row.Min1!=row.Min2||row.Max1!=row.Max2) {
              // console.log(row.Min1!=row.Min2||row.Max1!=row.Max2)
              return 'diff';
            }
            return '';
          },
          aaa(column, event){
            // console.log(column)
            // console.log(event)
            if(column.label=='-'){
              return
            }
            let fileName = [
              'Astralian diesel fuels.xlsx',
              'GB 0# General diesel fuels 2015.pdf',
              'India HSD.pdf',
              'GB(V) 0# AutomobiIe dieseI fuels 2016.pdf',
              'GB(VI) 0# AutomobiIe dieseI fuels 2016.pdf',
              'Hong Kong Motor Vehicle  ULSD.pdf',
              'Sinochem QZ 50ppm diesel fuels (Philippine).pdf',
              'Indonesia HSD.pdf',
              'Philippine diesel fuels.xlsx',
              'USA MVNRLM Motor vehicle diesel fuel.pdf',
              'Australian Automotive Diesel  2001.pdf'
            ]
            for(let i=0;i<fileName.length;i++){
              (function (i) {
                if(fileName[i].indexOf(column.label)!=-1){
                  // let reader = new FileReader();
                  // reader.readAsDataURL(fileName[i]);

                  // console.log(encodeURI(configURL.infoUrl + '/news/static/files/'+fileName[i]))
                  // window.open(encodeURI('/news/static/files/'+fileName[i]))
                  // window.open('/news/static/files/'+fileName[i])
                }
              })(i)
            }
          },
          dealCreatetale(){
            this.tableData = createTableData
            this.tableData = this.copyCreateTableData.map((field,index)=>{
              // console.log(field['Properties'],allData[this.materialName2][field['Properties']])
              field.Min1 = allData[this.materialName1][field['Properties']].Min
              field.Max1 = allData[this.materialName1][field['Properties']].Max
              field.Min2 = allData[this.materialName2][field['Properties']].Min
              field.Max2 = allData[this.materialName2][field['Properties']].Max
              return field
            })
            this.tableData.forEach((field,index,arr)=>{
                if(field.Min1==""&&field.Max1==""&&field.Min2==""&&field.Max2==""){
                  arr.splice(index,1)
                  index-=1
                }
            });
            this.clearTableDataTrim()
            // console.log(this.tableData)
          },
          //清空无值的情况
          clearTableDataTrim(){
            for(let i = 0;i<this.tableData.length;i++){
              if(this.tableData[i].Min1==""&&this.tableData[i].Max1==""&&this.tableData[i].Min2==""&&this.tableData[i].Max2==""){
                this.tableData.splice(i,1)
                i= i-1;
              }
            }
          },
          matchComparison(){
            let query = this.$route.query
            if(!Object.isNotEmpty(query)){
                $('.el-table__empty-text').text('Please choose quality standards.')
                return
            }
            if(query.material1==query.material2){
              this.dialogVisible = true;
              return
            }
            this.ruleForm.material1 = query.material1
            this.ruleForm.material2 = query.material2

            for(let i = 0;i<this.options.length;i++){
                if(this.ruleForm.material1 == this.options[i].value){
                    this.materialName1 = this.options[i].name
                    break
                }
            }
            for(let i = 0;i<this.options2.length;i++){
              if(this.ruleForm.material2 == this.options2[i].value){
                this.materialName2 = this.options2[i].name
                break
              }
            }
            this.dealCreatetale()
          },
          submitForm(formName){
            this.$refs[formName].validate((valid)=>{
              this.checkedName = false;
              if(valid){
                this.$router.push({path:'/properties',
                  query:{
                    material1:this.ruleForm.material1,
                    material2:this.ruleForm.material2
                  }})
                  this.matchComparison()

              }else{
                console.log('error submit!!');
                return false;
              }
            })
          },
            contrast(){
              // console.log(this.checkedName)
                if(this.checkedName){
                    this.tdata = this.tableData.filter(function(item){
                        return item.Min1!=item.Min2||item.Max1!=item.Max2
                    })
                }else{
                }


            }
        }
    }
</script>

<style scoped>
  .properties-comparison{
    margin-bottom: 30px;
  }
  .comparison-ruleForm{
    padding-top: 31px;
  }
  .title{
    font-family: ArialMT;
    font-size: 20px;
    color: #F5650A;
    margin: 30px 0;
    font-weight: normal;
  }
  .search-condition{
    width: 1180px;
    /*height: 101px;*/
    background: #F5F5F5;
  }
  .compare-box{
    width: 300px;
    height: 188px;
    background: #F5F5F5;
    padding: 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  .compare-btn{
    width: 100px;
    height: 36px;
    background: #F36523;
    border: 1px solid #F36523;
    border-radius: 2px;
    margin: 0 auto;
    cursor: pointer;
    font-family: ArialMT;
    font-size: 14px;
    color: #fff;
    letter-spacing: 0;
  }
  .compare-select{

  }
  .dinlineb{
    display: inline-block;
  }
  .vs{
    font-family: Arial-BoldMT;
    font-size: 24px;
    color: #F36523;
    letter-spacing: 0;
    vertical-align: middle;
    margin: 0 20px;
  }
</style>



